// TYPOGRAPHY SIZE AND LINE HEIGHT

h1, .h1 {
	font-size: $h1-font-size;
	line-height: $font-h1-line-height;
}

h2, .h2 {
	font-size: $h2-font-size;
	line-height: $font-h2-line-height;
}

h3, .h3 {
	font-size: $h3-font-size;
	line-height: $font-h3-line-height;
}

h4, .h4 {
	font-size: $h4-font-size;
	line-height: $font-h4-line-height;
}

h5, .h5 {
	font-size: $h5-font-size;
	line-height: $font-h5-line-height;
}

h6, .h6 {
	font-size: $h6-font-size;
	line-height: $font-h6-line-height;
}

p, .p, a {
	font-size: $font-size-sm;
}

a.link-box{
	text-decoration: none;
}

label {
	font-size: $font-size-sm;
	color: $dark;
	padding-bottom: $spacer-2;
}

.clickable{
	cursor: pointer;
}

.text-primary {
	@if $light-theme {
		color: $text-dark !important;

		&:hover{
			text-decoration-color: $text-dark !important;
		}
	} @else {
		color: $primary;
	}
}

.fine-print{
	font-size: $font-size-xs;
	padding-bottom: 0;
	margin-bottom: 0;
}

.aura-page-headers{
	text-align: center;
	margin-bottom: $spacer-12;

	.page-header{
		color: $dark;
		padding: 0;
		text-align: center;
		font-weight: $font-weight-light;
		//font-size: $font-size-xl;
	}
	
	.page-subheader{
		color: $dark;
		//font-weight: $font-weight-light;
		text-align: center;
		font-size: $font-size-base;
		margin-bottom: 0;
		line-height: 1.5rem;
	}

	.page-desc{
		text-align: center;
		padding-top: $spacer-8;
		font-size: $font-size-sm;
	}
}

.merchandise-header{
	@extend .page-header;
}

.merchandise-subheader{
	@extend .page-subheader;
}

.section-header{
	color: $dark;
	margin-bottom: $spacer-12;
	padding-bottom: $spacer-2;
	padding-top: $spacer-4;
	font-weight: $font-weight-normal;
	border-bottom: 1px solid $lighter;
	text-transform: uppercase;
	font-size: $h4-font-size;
	//font-size: $font-size-base;
}

.section-header-centered{
	text-align: center;
	color: $dark;
	font-weight: $font-weight-normal;
	font-size: $font-size-lg;

	+ p{
		text-align: center;
		color: $dark;
		font-weight: $font-weight-light;
	}
}

.header-thanks{
	color: $dark;
	font-size: $font-size-xl;
	margin-top: $spacer-12;
}

.header-confirmation-email{
	color:  text-color-light;
}

.header-thanks-details{
	border: 1px solid $lighter;
	border-radius: 8px;
	padding: $spacer-8;
	font-size: $font-size-sm;
	min-width: 80%;

	dl{
		margin-bottom: 5px;
		line-height: 1rem;
	}

	dd{
		margin-bottom: 0;
	}
}

.checkbox-opt-in-text{
	line-height: $font-h6-line-height;
}

.btn {
	font-size: 1rem !important;
	text-transform: uppercase;
}